import { request } from 'utils';

const prefix = '/api/education';

export const getFacultyByRegisterSession = ({
  registerSessionId,
  page,
  pageSize=10,
  orderBy,
  isPagingEnabled=true
} = {}) =>
  request({
    prefix,
    url: '/GradePartGrading/FacultyByRegisterSession',
    method: 'GET',
    params: {
      registerSessionId,
      page,
      pageSize,
      orderBy,
      isPagingEnabled
    },
    data: {}
  });


export const createGradePartGrading = data =>
  request({
    prefix,
    url: '/GradePartGrading',
    method: 'POST',
    params: {},
    data
  });




