import React, { Component } from 'react';
import { Table, Checkbox, Switch } from 'antd';
import { isEmpty } from 'lodash';
import { BtnDelete } from 'components/common/button';

class TableEnteringProcessScore extends Component {
  constructor(props) {
    super(props);
    this.state = {
      data: []
    };

    this.rowSelection = {
      onChange: (selectedRowKeys, selectedRows) => {
        console.log(`selectedRowKeys: ${selectedRowKeys}`, 'selectedRows: ', selectedRows);
      },
      getCheckboxProps: record => ({
        disabled: record.name === 'Disabled User', // Column configuration not to be checked
        name: record.name,
      }),
    };
  }

  componentWillReceiveProps(nextProps) {
    console.log(nextProps);
    if(nextProps.reload !== this.reload) {
      this.setState({ data: this.parseGradePartTableData(nextProps.data) });
    }
  }

  parseGradePartTableData = rawData => (
    rawData.map(item => {
      item.gradeParts.forEach((part, index) => {
        item[`gradePart_${index + 1}`] = part.grade
      })
      return item;
    })
  )

  renderChildRow = () => {
    const { data } = this.state;
    console.log(data)
    if(!isEmpty(data)) {
      const { gradeParts } = data[0];
      return gradeParts.map((part, index) => (
        {
          title: part.gradePartLabel,
          dataIndex: `gradePart_${index + 1}`,
          align: 'center'
        }
      ))
    }
    return [];
  }

  render() {
    const columns = [
      // {
      //   title: 'Mã sinh viên',
      //   dataIndex: 'masv',
      //   key: 'masv',
      // },
      {
        title: 'Họ và tên',
        dataIndex: 'fullname',
        key: 'fullname',
      },
      {
        title: 'Tên lớp',
        dataIndex: 'className',
        key: 'className',
      },
      {
        title: 'Số tiết nghỉ',
        dataIndex: 'noOfLeave',
        key: 'noOfLeave',
      },
      {
        title: 'Điểm thành phần',
        children: this.renderChildRow()
      },
      {
        title: 'Điểm chuyển / miễn chuyển',
        dataIndex: 'isTransmit',
        // width: '100px',
        align: 'center',
        render: isTransmit => <Checkbox checked={isTransmit} disabled />
      },
      {
        title: 'Thao tác',
        dataIndex: 'operation',
        render: () => (
          <div style={{ textAlign: 'center' }}>
            <Switch defaultChecked />
            <BtnDelete type="error" className="btnSmallAddInTable" onClick={this.showModal}>
              Xóa
            </BtnDelete>
          </div>
        ),
      },
    ];
    const { data } = this.state;
    return (
      <div>
        <p>
          <Checkbox /> Chọn mặc định điểm chuyên cần (Điểm 10)
        </p>
        <Table
          bordered
          rowSelection={this.rowSelection}
          dataSource={data}
          columns={columns}
        />
      </div>
    );
  }
}

export default TableEnteringProcessScore;



