import React, { Component } from 'react';
import Page from 'components/LayoutComponents/Page';
import { Row, Col, Form, Divider, Card } from 'antd';
import { isEmpty } from 'lodash';
import { CartHeadAction } from 'components/common';
import { Btn, BtnSave, BtnExport } from 'components/common/button';
import { getStudentInClassCredit } from 'services/classCreditSchedule';
// import MenuTabTop from './MenuTabTop';
import TableEnteringProcessScore from './TableEnteringProcessScore';
import FormInfoCommon from './FormInfoCommon';
import GradePartPopup from './PopupGradePart';

@Form.create({ name: 'EnteringProcessScorePage' })
class EnteringProcessScorePage extends Component {
  state = {
    visible: false,
    selectedGradeParts: [],
    reload: 1,
    tableData: []
  };

  setSelectedGradeParts = selectedGradeParts => {
    console.log(selectedGradeParts)
    this.setState({ selectedGradeParts })
  }

  handleSubmit = e => {
    e.preventDefault();
    this.props.form.validateFields((err, values) => {
      if (!err) {
        console.log('Received values of form: ', values);
      }
    });
  };

  handleSearch = async({ id }) => {
    const { selectedGradeParts } = this.state;
    const result = await getStudentInClassCredit({ id, gradeParts: selectedGradeParts });
    if(result.data && result.data.items) {
      this.setState({ tableData: result.data.items, reload: this.state.reload + 1 })
    }
  }
  
  toggleGradePartPopup = visible => {
    this.setState({ visible });
  }

  render() {
    const { visible, selectedGradeParts, tableData, reload } = this.state;
    console.log(tableData)
    return (
      <Card title="Danh sách nhập điểm lớp quá trình tín chỉ">
        <CartHeadAction>
          <BtnSave title="Lưu" />
          <Btn title="Khóa điểm" />
          <Btn title="Xóa tất cả" />
          <Btn title="Điểm thành phần" onClick={() => this.toggleGradePartPopup(true)} />
          <BtnExport disabled={isEmpty(tableData)} />
        </CartHeadAction>
        {/* <Row className="rowMargin">
          <Col span={24}>
            <MenuTabTop toggleGradePartPopup={this.toggleGradePartPopup} />
          </Col>
        </Row> */}
        <Row span={24}>
          <FormInfoCommon
            selectedGradeParts={selectedGradeParts}
            handleSearch={this.handleSearch}
          />
        </Row>
        <Divider>Danh sách lớp tín chỉ</Divider>
        <Row>
          <Col span={24}>
            <TableEnteringProcessScore data={tableData} reload={reload} />
          </Col>
        </Row>
        <GradePartPopup
          visible={visible}
          toggleGradePartPopup={this.toggleGradePartPopup}
          setSelectedGradeParts={this.setSelectedGradeParts}
        />
      </Card>
    );
  }
}

export default props => (
  <Page {...props}>
    <EnteringProcessScorePage />
  </Page>
);



